import React from 'react';

export default class Article extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            articles: []
        }
    }

    generateRandomArticles() {
        const temps = [];
        for (let i = 0; i < 20; i++) {
            const article = {
                title: "Article title " + i,
                description: "Lorem ipsum dolor sit amet",
                image: "data:image/jpeg;base64,",
                last_updated: "5 mins ago"
            }
            temps.push(article)
        }
        this.setState({
            articles: temps
        })
    }

    componentDidMount() {
        this.generateRandomArticles()
    }

    render() {
        return (
            <div className="row">
                {
                    this.state.articles.map((article, index) => {
                        return (
                            <div className="card col-md-4 p-4">
                                <img src={article.image} className="card-img-top cover" alt="..." />
                                <div className="card-body text-center">
                                    <h5 className="card-title">{article.title}</h5>
                                    <p className="card-text">{article.description}</p>
                                    <p className="card-text">{article.last_updated}</p>
                                    <a href="#" class="btn btn-primary">Selengkapnya</a>
                                </div>

                            </div>
                        )
                    })
                }

            </div>

        );
    }
}